.changed-file {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-width: 0;
  max-height: 100%;

  .header {
    span {
      user-select: text;
    }

    background: var(--box-alt-background-color);
    border-bottom: 1px solid var(--diff-border-color);

    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    flex-grow: 0;
    align-items: center;

    padding: var(--spacing-half) var(--spacing);

    @include octicon-status;

    .diff-options-component {
      margin-right: var(--spacing-half);
      flex-shrink: 0;
    }

    .octicon.status {
      flex-shrink: 0;
      vertical-align: text-bottom;
      position: relative;
      align-self: center;
      flex-shrink: 0;
    }

    .line-endings {
      margin-right: var(--spacing-half);
    }

    .open-merge-tool {
      margin-right: var(--spacing-half);
    }
  }
}
